.main{
    width: 100%;
    height: 100%;
    text-align: center;
}
.access-wrapper{
    display: inline-block;
    text-align: center;
    margin-top: 5%;
    width: 40%;
    height: 50%;
    border-radius: 2px;
    background: #FFFFFF;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.login-div{
    display: inline-block;
    margin-top: 5%;
    width: 60%;
    height: 90%;
    max-height: 90%;
    overflow-y: scroll;
    overflow: -moz-scrollbars-none; 
    /* background: #f2f2f2; */
}
.login-div {
    -ms-overflow-style: none;  /* IE 10+ */
    overflow: -moz-scrollbars-none;  /* Firefox*/
}
.login-div:-webkit-scrollbar { 
    display: none;  
    /* Safari and Chrome */
} 
.input-field{
    width: 100%;
}
.signup-btn{
    width: 100%;
    height: 15%;
    margin-top: 5%;
    margin-bottom: 5%;
    border-radius: 3px;
    border: none;
}
.main-header-login{
    font-size: 1.4vw;
    font-weight: bold
}
Button:focus{
    outline: none;
}
.display-social{
    display: none;
}

