.App {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  overflow-y: scroll;
  background: #1c92d2;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #f2fcfe, #70e1f5);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #f2fcfe, #70e1f5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  pointer-events: auto;
}
.App-main {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  overflow-y: scroll;
  background: white;  /* fallback for old browsers */
  text-align: center;
  pointer-events: auto;
}
Router{
  width: 100%;
  height: 100%;
}
.navbar-section{
  width: 100%;
  height: 8%;
}
.pages{
  width: 100%;
  height: 92%;
}
.App-link {
  color: #61dafb;
}
#overlay {
  padding-top:20%;
  display: inline-block;
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.3);
  z-index: 2;
  cursor: pointer;
}
/* Generic usable classes */
.full-height{
  height: 100%;
}