.home-main{
    width: 100%;
    height: 100%;
    padding-top: 2%;
}
.item-div{
    padding-top:2%;
    background: #FFFFFF;
}
.vendor-div{
    padding-top:2%;
    background: #FFFFFF;
}
.divider-line{
    width: 100%;
    height: 1px;
    background: #E0E0E0;
    overflow-y: scroll;
}
.vendor-card-div{
    height: 30%;
    padding: 1%;
}
.item-card-div{
    height: 9%;
    padding: 1%;
}
.checkout-btn{  
    width: 100%;
    height: 80%;
    margin: 1%;
    background: #66BB6A;
    cursor: pointer;
    color: white;
    padding-top: 4%;
    font-size: 1vw;
    border-radius: 3px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 2px 1px -2px rgba(0, 0, 0, 0.2);
}