html{
  height: 100%;
  width: 100%;
  overflow: hidden;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root{
  width: 100%;
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
::-webkit-scrollbar { 
  display: none; 
}

.navbar-space{
    height: 100%;
    width: 100%;
    font-size: 1vw;
  }
  nav.navbar, .navbar-default {
    background-color: #FFFFFF !important;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    height: 95%;
    width: 100%;
  }
  .app-logo{
    height: 100%;
    width: 30%;
    max-width: 100%;
    max-height: 100%;
  }
  .navbar-default {
    background-image: none;
    border: none;
    border-radius: 0;
  }
  
  .navbar-default .navbar-nav > li > a {
    color: #757575;
    font-size: 1.1vw;
  }
  
  .navbar-default .navbar-brand {
    color: #757575;
  }
  
  .navbar-default .navbar-toggle {
    border-color: black;
  }
  
  .navbar-default .navbar-toggle .icon-bar {
    background-color: white;
  }
  
  .navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
    background-color: transparent;
  }
  
  .navbar-default .navbar-brand:focus, .navbar-default .navbar-brand:hover {
    color: white;
    background-color: transparent;
  }
  
  .navbar-default .navbar-nav>li>a:focus, .navbar-default .navbar-nav>li>a:hover {
    color: #616161;
  }
  
  .nav-style:hover{
    font-weight: bold;
    color: #4e54c8;
  }
  .navbar-span{
    font-size: 1.2vw;
    margin-top: 8px;
  }
.main{
    width: 100%;
    height: 100%;
    text-align: center;
}
.access-wrapper{
    display: inline-block;
    text-align: center;
    margin-top: 5%;
    width: 40%;
    height: 50%;
    border-radius: 2px;
    background: #FFFFFF;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.login-div{
    display: inline-block;
    margin-top: 5%;
    width: 60%;
    height: 90%;
    max-height: 90%;
    overflow-y: scroll;
    overflow: -moz-scrollbars-none; 
    /* background: #f2f2f2; */
}
.login-div {
    -ms-overflow-style: none;  /* IE 10+ */
    overflow: -moz-scrollbars-none;  /* Firefox*/
}
.login-div:-webkit-scrollbar { 
    display: none;  
    /* Safari and Chrome */
} 
.input-field{
    width: 100%;
}
.signup-btn{
    width: 100%;
    height: 15%;
    margin-top: 5%;
    margin-bottom: 5%;
    border-radius: 3px;
    border: none;
}
.main-header-login{
    font-size: 1.4vw;
    font-weight: bold
}
Button:focus{
    outline: none;
}
.display-social{
    display: none;
}


.vendor-card-main{
    height: 100%;
    width: 100%;
    border-radius: 7px;
    border: 1px solid #E0E0E0;
    padding: 2%;
}
.read-more{
  color: #1976D2;
  cursor: pointer;
}
.read-more:hover{
    text-decoration: underline;
}
.inputStyle{
  width : 100%;
  border : 0px;
  text-align: center;
  margin-top: 5%;
}
.inputStyle:focus{
  outline: none;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
.item-card-main{
    height: 100%;
    width: 100%;
    border-radius: 2px;
    border: 1px solid #E0E0E0;
    padding: 2%;
    cursor: pointer;
    /* box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); */
}
.home-main{
    width: 100%;
    height: 100%;
    padding-top: 2%;
}
.item-div{
    padding-top:2%;
    background: #FFFFFF;
}
.vendor-div{
    padding-top:2%;
    background: #FFFFFF;
}
.divider-line{
    width: 100%;
    height: 1px;
    background: #E0E0E0;
    overflow-y: scroll;
}
.vendor-card-div{
    height: 30%;
    padding: 1%;
}
.item-card-div{
    height: 9%;
    padding: 1%;
}
.checkout-btn{  
    width: 100%;
    height: 80%;
    margin: 1%;
    background: #66BB6A;
    cursor: pointer;
    color: white;
    padding-top: 4%;
    font-size: 1vw;
    border-radius: 3px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 2px 1px -2px rgba(0, 0, 0, 0.2);
}
.checkout-card-main{
    height: 100%;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    border: 1px solid #E0E0E0;
    padding-bottom: 2%;
}
.remove-item{
    color: #FF5722;
    font-size : 0.9vw;
    text-align : start;
    padding-top : 1.5%;
    cursor: pointer;
}
.remove-item:hover{
    text-decoration: underline;
}
.checkout-main{
    width: 100%;
    height: 100%;
    padding-top: 2%;
}
.checkout-item-list{
    border-right: 1px solid #E0E0E0;
}
.checkout-item-card-div{
    min-height: 10%;
    padding: 1%;
}
.App {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  overflow-y: scroll;
  background: #1c92d2;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left bottom, left top, from(#f2fcfe), to(#70e1f5));
  background: linear-gradient(to top, #f2fcfe, #70e1f5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  pointer-events: auto;
}
.App-main {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  overflow-y: scroll;
  background: white;  /* fallback for old browsers */
  text-align: center;
  pointer-events: auto;
}
Router{
  width: 100%;
  height: 100%;
}
.navbar-section{
  width: 100%;
  height: 8%;
}
.pages{
  width: 100%;
  height: 92%;
}
.App-link {
  color: #61dafb;
}
#overlay {
  padding-top:20%;
  display: inline-block;
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.3);
  z-index: 2;
  cursor: pointer;
}
/* Generic usable classes */
.full-height{
  height: 100%;
}
