.checkout-card-main{
    height: 100%;
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    border: 1px solid #E0E0E0;
    padding-bottom: 2%;
}
.remove-item{
    color: #FF5722;
    font-size : 0.9vw;
    text-align : start;
    padding-top : 1.5%;
    cursor: pointer;
}
.remove-item:hover{
    text-decoration: underline;
}