.vendor-card-main{
    height: 100%;
    width: 100%;
    border-radius: 7px;
    border: 1px solid #E0E0E0;
    padding: 2%;
}
.read-more{
  color: #1976D2;
  cursor: pointer;
}
.read-more:hover{
    text-decoration: underline;
}
.inputStyle{
  width : 100%;
  border : 0px;
  text-align: center;
  margin-top: 5%;
}
.inputStyle:focus{
  outline: none;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}